<template>
  <div id="root">
    <div
      class="wrapper"
      style="height: 945px; padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left); transition: padding 100ms, height 100ms;"
    >
      <div class="settingschange-wrapper">
        <div class="settingschange-container">
          <div class="settingschange-title"></div>
          <div class="settingschange-select">
            <!--     -->
            <div class="settingschange-select-button" @click="setDarkTheme">
              
              <img v-if="currentTheme === 'dark'" :src="galka" alt="  ">
            </div>
            <!--     -->
            <div class="settingschange-select-button" @click="setLightTheme">
              
              <img v-if="currentTheme === 'light'" :src="galka" alt="  ">
            </div>
          </div>
        </div>
        <router-link class="nazad" to="/settings"></router-link>
      </div>
    </div>
    <div id="veepn-breach-alert"></div>
  </div>
</template>

<script>
import { onMounted, ref } from 'vue';

export default {
  setup() {
    //      
    const currentTheme = ref('light');

    //     require()
    const galka = require('./img/galka.svg');

    //     
    const setDarkTheme = () => {
      document.body.classList.add('dark');
      localStorage.setItem('theme', 'dark');
      currentTheme.value = 'dark';
    };

    //     
    const setLightTheme = () => {
      document.body.classList.remove('dark');
      localStorage.setItem('theme', 'light');
      currentTheme.value = 'light';
    };

    //       localStorage
    onMounted(() => {
      const savedTheme = localStorage.getItem('theme');
      if (savedTheme === 'dark') {
        document.body.classList.add('dark');
        currentTheme.value = 'dark';
      } else {
        document.body.classList.remove('dark');
        currentTheme.value = 'light';
      }
    });

    return {
      setDarkTheme,
      setLightTheme,
      currentTheme,
      galka,
    };
  },
};
</script>

<style scoped>
.nazad {
  margin-top: 20px;
}

/*       */
.settingschange-select-button {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 10px;
}

.settingschange-select-button img {
  margin-left: 5px;
  width: 16px;
  height: 16px;
}
</style>
