<template>
  <div class="flex">
    <!-- Sidebar -->
    <div
      id="sidebar"
      :class="['sidebar__wrapper', { 'sidebar__wrapper-active': sidebarActive }]"
      :style="{ display: sidebarDisplay ? 'flex' : 'none' }"
    >
      <div class="sidebar">
        <div class="sidebar__user">
          <div class="sidebar__user-avatar"><img :src="model_logo" alt=""></div>
          <div class="sidebar__user-container">
            <div class="sidebar__user-name">Monkey AI</div>
            <div class="sidebar__user-subscribe"></div>
          </div>
        </div>
        <div class="sidebar__nav">
          <a href="/?model=gpt-4o-mini" class="sidebar__item">
            <!-- ,   -->
            <img :src="newChatSvg" alt=" " />
            <div class="sidebar__item-text"> </div>
          </a>
          <router-link to="/settings"  class="sidebar__item">
            <img :src="settingsSvg" alt="" />
            <div class="sidebar__item-text"></div>
          </router-link>
        </div>
      </div>
      <div class="sidebar__close-wrapper">
        <button class="sidebar__close" @click="toggleSidebar">
          <img :src="closeSvg" alt="" />
        </button>
      </div>
    </div>

    <!--     -->
    <div
      class="gpt-model-select-wrapper"
      ref="modelDropdown"
      :class="{ visible: modelDropdownVisible }"
      @click="closeModelDropdown"
    >
      <!--   ,          -->
      <div class="gpt-model-select-container" @click.stop>
        <button
          class="gpt-model-select-item"
          v-for="(model, index) in models"
          :key="index"
          @click="selectModel(model)"
        >
          <div class="gpt-model-select-text">
            <div class="gpt-model-select-modelname" v-html="model.name"></div>
            <div class="gpt-model-select-description">{{ model.description }}</div>
          </div>
          <div class="gpt-model-select-logo">
            <img :src="model.logo" alt="" />
          </div>
        </button>
      </div>
    </div>

    <!-- Header -->
    <header>
      <div class="header__sidebar" @click="toggleSidebar">
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
             xmlns="http://www.w3.org/2000/svg">
          <path d="M3 8.5H21" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
          <path d="M3 15.5H16" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
        </svg>
      </div>
      <div class="header__model" ref="modelSelector" @click="toggleModelDropdown">
        <div class="header__model-name" v-html="selectedModel"></div>
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
             xmlns="http://www.w3.org/2000/svg">
          <path d="M16 10L12 14L8 10" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
        </svg>
      </div>
      <div class="header__limits"></div>
    </header>

    <!--   -->
    <div class="chat-wrapper">
      <div class="chat-content" :class="{ empty: chatMessages.length === 0 }" ref="chatContent">
        <!--    ,    -->
        <template v-if="chatMessages.length === 0">
          <div class="ui-emptyChatCard">
            <div class="ui-ecc-title">   ?</div>
            <div class="ui-ecc-text"> ,     ChatGPT</div>
            <div style="width: 112px; height: 112px;">
              <!--     -->
              <img :src="emptyCardImage" alt=" " />
            </div>
          </div>
        </template>
        <!--     -->
        <template v-else>
          <div
            v-for="(msg, index) in chatMessages"
            :key="index"
            :class="['message', msg.type === 'user' ? 'user-message' : 'bot-message']"
          >
            <!--   -->
            <span v-if="msg.type === 'user'">{{ msg.text }}</span>
            <!--      -->
            <div v-else>
              <div v-html="formatMessage(msg.text)"></div>
              <button class="copy-button" @click="copyToClipboard(msg.text)">
                <img :src="copy" alt="">
              </button>
            </div>
          </div>
        </template>
      </div>
    </div>

    <!-- Footer    -->
    <footer>
      <form class="footer__input" @submit.prevent="sendMessage">
        <div class="input__text-wrapper">
          <div class="input__text null">
            <div
              id="editable-message-text"
              contenteditable="true"
              role="textbox"
              dir="ltr"
              ref="editableDiv"
              @input="onInput"
              @keydown="onKeydown"
            ></div>
            <span v-if="messageText.trim() === ''" class="input__text-placeholder">
                
            </span>
          </div>
        </div>
        <label class="input__submit" :class="{ disabled: isSubmitDisabled }">
          <button
            :disabled="isSubmitDisabled"
            id="chat__input-submitbutton"
            type="submit"
          ></button>
          <img :src="updataSvg" alt="" />
        </label>
      </form>
    </footer>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount, nextTick } from 'vue';

//      
const newChatSvg  = require('./img/new_chat.svg');
const settingsSvg = require('./img/settings.svg');
const closeSvg    = require('./img/close.svg');
const updataSvg   = require('./img/updata.svg');
const copy        = require('./img/copy.svg');

//   ""  
const monkeyChemistry = require('./img/monkey/chemistry.svg');
const monkeyHacker    = require('./img/monkey/hacker.svg');
const monkeyHappy     = require('./img/monkey/happy.svg');
const monkeyIdea      = require('./img/monkey/idea.svg');
const monkeyQuestion  = require('./img/monkey/question.svg');
const monkeyRead      = require('./img/monkey/read.svg');
const monkeyShock     = require('./img/monkey/shock.svg');
const monkeyWork      = require('./img/monkey/work.svg');
const monkeyWorkout   = require('./img/monkey/workout.svg');
const model_logo      = require('./img/model_logo.svg');


/* ===  === */
const sidebarActive = ref(false);
const sidebarDisplay = ref(false);
function toggleSidebar() {
  if (sidebarActive.value) {
    sidebarActive.value = false;
    setTimeout(() => {
      sidebarDisplay.value = false;
    }, 200);
  } else {
    sidebarDisplay.value = true;
    setTimeout(() => {
      sidebarActive.value = true;
    }, 10);
  }
}

/* ===   === */
const models = [
  {
    name: 'ChatGpt <span>4o mini</span>',
    description: '  ',
    logo: require('./img/model.svg')
  },
  {
    name: 'Dall-E <span>3</span>',
    description: ' ',
    logo: require('./img/dallE.svg')
  }
];
const selectedModel = ref(models[0].name);
const modelDropdownVisible = ref(false);
const modelSelector = ref(null);
const modelDropdown = ref(null);

function toggleModelDropdown(e) {
  e.stopPropagation();
  modelDropdownVisible.value = !modelDropdownVisible.value;
}

//         
function closeModelDropdown() {
  modelDropdownVisible.value = false;
}

function selectModel(model) {
  selectedModel.value = model.name;
  modelDropdownVisible.value = false;
}

function handleDocumentClick(e) {
  if (modelSelector.value && modelDropdown.value) {
    if (
      !modelSelector.value.contains(e.target) &&
      !modelDropdown.value.contains(e.target)
    ) {
      modelDropdownVisible.value = false;
    }
  }
}

/* ===  === */
const messageText = ref('');
const chatMessages = ref([]);
const editableDiv = ref(null);
const chatContent = ref(null);

const isSubmitDisabled = computed(() => messageText.value.trim() === '');

function onInput(e) {
  messageText.value = e.target.innerText;
}
function onKeydown(e) {
  if (e.key === 'Enter' && !e.shiftKey) {
    e.preventDefault();
    sendMessage();
  }
}

async function sendMessage() {
  const text = messageText.value.trim();
  if (!text) return;

  chatMessages.value.push({ text, type: 'user' });
  messageText.value = '';
  if (editableDiv.value) {
    editableDiv.value.innerText = '';
  }
  await nextTick();
  if (chatContent.value) {
    chatContent.value.scrollTop = chatContent.value.scrollHeight;
  }

  chatMessages.value.push({ text: '...', type: 'bot' });
  const botMsgIndex = chatMessages.value.length - 1;

  try {
    const apiKey = process.env.VUE_APP_OPENAI_API_KEY;

    const response = await fetch("https://api.openai.com/v1/chat/completions", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${apiKey}`
      },
      body: JSON.stringify({
        model: "gpt-3.5-turbo",
        messages: [{ role: "user", content: text }]
      })
    });
    const data = await response.json();
    if (data.error) {
      throw new Error(data.error.message || " API");
    }
    const answer = data.choices[0].message.content;
    chatMessages.value[botMsgIndex] = { text: answer, type: 'bot' };
  } catch (error) {
    chatMessages.value[botMsgIndex] = {
      text: "   : " + error.message,
      type: 'bot'
    };
  } finally {
    await nextTick();
    if (chatContent.value) {
      chatContent.value.scrollTop = chatContent.value.scrollHeight;
    }
  }
}

function formatMessage(text) {
  return text.replace(/\n/g, '<br>');
}

/* ===  ""    === */
const emptyCardImage = ref('');
onMounted(() => {
  const images = [
    monkeyChemistry,
    monkeyHacker,
    monkeyHappy,
    monkeyIdea,
    monkeyQuestion,
    monkeyRead,
    monkeyShock,
    monkeyWork,
    monkeyWorkout
  ];
  emptyCardImage.value = images[Math.floor(Math.random() * images.length)];

  document.addEventListener('click', handleDocumentClick);
});
onBeforeUnmount(() => {
  document.removeEventListener('click', handleDocumentClick);
});

function copyToClipboard(text) {
  navigator.clipboard.writeText(text)
    .then(() => {
      //   
    })
    .catch(err => {
      console.error("  : ", err);
    });
}
</script>

<style>
/*     */
</style>
