/**
 *  shareApp   Telegram.WebApp.share  Telegram Web Apps API.
 *   ,     .
 */
 function shareApp() {
  const text = encodeURIComponent("  ! \nhttps://t.me/Monkey_GPTbot");
  const shareUrl = `https://t.me/share/url?url=${text}`;

  if (window.Telegram && Telegram.WebApp) {
    Telegram.WebApp.openTelegramLink(shareUrl);
  } else {
    window.open(shareUrl, "_blank"); //   ,  WebApp API  
  }
}
